<script>
//import ColorTheme from './ColorTheme'
import HoaDonCT from '@/components/ComTest2/HoaDonCT'
const types={
    HOA_DON_GET_DS:'HoaDon/lds',
    HOA_DON_LDS:'HoaDon/lds',
    HOA_DON_ADD:'HoaDon/add',
    HOA_DON_DEL:'HoaDon/del',
    HOA_DON_UPDATE:'HoaDon/update',    


    HOA_DON_CT_GET_DS:'HoaDonCT/lds',
    HOA_DON_CT_GET_BY_HD_ID:'HoaDonCT/getByHdId',//this.$store.getters[](1);//<----- Remember!!!
    HOA_DON_CT_LDS:'HoaDonCT/lds',

    HOA_DON_CT_ADD:'HoaDonCT/add',
    HOA_DON_CT_DEL:'HoaDonCT/del',
    HOA_DON_CT_UPDATE:'HoaDonCT/update',    



}
export default {
  name: 'HoaDon.vue',
  components: { 
    //ColorTheme 
    HoaDonCT,
  },
  computed:{
    ds(){
      return this.$store.getters[types.HOA_DON_GET_DS];
    }
  },
  data(){
    return{
      name:`${this.$options.name}`,
      url:'',
      sel:{}
    }
  },
  created(){
    this.$store.dispatch(types.HOA_DON_LDS).then(res=>{
      //alert(JSON.stringify(res));
    })
    this.$store.dispatch(types.HOA_DON_CT_LDS).then(res=>{
      //alert(JSON.stringify(res));
    })


  },
  methods:{
    on_change(v){
      //alert(JSON.stringify(v));
      this.sel.tien=v.tong;
    },
    on_del(v){
      this.$store.dispatch(types.HOA_DON_DEL,v.id).then(res=>{
      })
    },
    on_edit(v){
      this.sel=Object.assign({},v);
    },
    on_update(){
      let r=Object.assign({},this.sel);
      this.$store.dispatch(types.HOA_DON_UPDATE,r).then(res=>{
        this.on_cancel();
      })
    },
    on_add(){
      this.sel={
        id:0,
        ten:'thuc 101',
        tien:0,
      }
    },
    on_do_add(){
      let r=Object.assign({},this.sel);
      //alert(JSON.stringify(r));
      this.$store.dispatch(types.HOA_DON_ADD,r).then(res=>{
        alert(JSON.stringify(res));
        //this.on_cancel();
      })
    },
    on_cancel(){
      this.sel={};
    }
  }
}
</script>
