<template>
  <div class="animated fadeIn border p-3">

        {{hoaDonId}}
        <div v-if=" sel.hasOwnProperty('id')==false ">
          <b-button v-if=" hoaDonId!=0 " @click="on_add()" size="sm" class="mr-1">add</b-button>
          <ul>
            <li v-for="(v,k,index) in ds" :key="index">
              {{v}}
              <b-button @click="on_edit(v)" size="sm" class="mr-1 mb-1">edit</b-button>
              <b-button @click="on_del(v)" size="sm" class="mr-1 mb-1">del</b-button>
            </li>
          </ul>
        </div>
        <div v-else>
          <!-- {{sel}} -->
          <h4 v-if="sel.id==0">Form Add</h4>
          <h4 v-else>Form Edit</h4>
          <b-row v-for="(v,k,index) in sel" :key="index" >
            <b-col cols="4">{{k}}</b-col>
            <b-col >
              <b-form-input v-if=" ['id','hd_id'].indexOf(k) ==-1 " class="mb-1" size="sm" v-model="sel[k]" :placeholder=" `Enter ${k}` "></b-form-input>              
              <b-form-input v-else class="mb-1" size="sm" v-model="sel[k]" :placeholder=" `Enter ${k}` " disabled ></b-form-input>              
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4"></b-col>
            <b-col>
              <b-button v-if="sel.id!=0" @click="on_update()" size="sm" class="mr-1" >update</b-button>
              <b-button v-else @click="on_do_add()" size="sm" class="mr-1">add</b-button>
              <b-button @click="on_cancel()" size="sm" class="mr-1">cancel</b-button>
            </b-col>
          </b-row>
        </div>


  </div>
</template>

<script>
//import ColorTheme from './ColorTheme'
const types={
    HOA_DON_CT_GET_DS:'HoaDonCT/lds',
    HOA_DON_CT_GET_BY_HD_ID:'HoaDonCT/getByHdId',//this.$store.getters[](1);//<----- Remember!!!
    HOA_DON_CT_LDS:'HoaDonCT/lds',

    HOA_DON_CT_ADD:'HoaDonCT/add',
    HOA_DON_CT_DEL:'HoaDonCT/del',
    HOA_DON_CT_UPDATE:'HoaDonCT/update',    
}
export default {
  name: 'HoaDonCT.vue',
  props:{
    hoaDonId:{
      type:Number
    },
    change:{
      type:Function
    }
  },
  components: { 
    //ColorTheme 
  },
  computed:{
    ds(){
      return this.$store.getters[types.HOA_DON_CT_GET_BY_HD_ID](this.hoaDonId);
    }
  },
  data(){
    return{
      name:`${this.$options.name}`,
      url:'',
      sel:{}
    }
  },
  created(){
    // this.$store.dispatch(types.HOA_DON_CT_LDS).then(res=>{
    //   //alert(JSON.stringify(res));
    // })
  },
  methods:{
    sumTien(){
      let kq=0;
      for(var x in this.ds){
        kq+= parseInt (this.ds[x].tien);
      }
      return kq;//this.ds.length;
    },
    on_del(v){
      this.$store.dispatch(types.HOA_DON_CT_DEL,v.id).then(res=>{
        this.$emit('change',{tong:this.sumTien()});
      })
    },
    on_edit(v){
      this.sel=Object.assign({},v);
    },
    on_update(){
      let r=Object.assign({},this.sel);
      //alert(JSON.stringify(r));
      this.$store.dispatch(types.HOA_DON_CT_UPDATE,r).then(res=>{
        //alert(JSON.stringify(res))
        this.$emit('change',{tong:this.sumTien()});
        this.on_cancel();
      })
    },
    on_add(){
      this.sel={
        id:0,
        ten:'thuc 101',
        tien:0,
        hd_id:this.hoaDonId
      }
    },
    on_do_add(){
      let r=Object.assign({},this.sel);
      //alert(JSON.stringify(r));
      this.$store.dispatch(types.HOA_DON_CT_ADD,r)
      .then(res=>{
        //alert(JSON.stringify(res));
        this.$emit('change',{tong:this.sumTien()});
        this.on_cancel();
      })
    },
    on_cancel(){
      this.sel={};
    }
  }
}
</script>
